//---------------------------------------------fonts
$font_path: "../fonts/";
$font_1: "ObjektivMk2", sans-serif;
$font_size_base: 1.6rem;
//---------------------------------------------fonts###

//---------------------------------------------colors
$white: #fff;
$black: #000;
$black_2: #132723;
$black_3: #172b29;

$orange: #ff6632;
$green: #60d28f;
$gray: #dde5e0;
$gray_1: #f6f8f7;
$gray_2: #a1a1a1;
$gray_3: #8c9894;
$gray_4: #f5f7f7;
$gray_5: #a6b5b1;

$color_text_base: $black_2;
$color_link_base: $green;
$body_bg: $white;
//---------------------------------------------colors###

//---------------------------------------------z-index
$z-index: (
	popup: 101,
	header: 100,
	footer: 100,
);
//---------------------------------------------z-index###

//---------------------------------------------layout
$page_width: 120rem;
$page_width_2: 136rem;
$page_width_3: 127rem;

$design_width: 1440;

//---------------------------------------------layout###

//---------------------------------------------global values
$gl_radius: 0.5rem;
//---------------------------------------------global values###

//---------------------------------------------media points
$point_0: 1600px;
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 480px;
$point_7: 375px;
$point_8: 320px;
$point_9: 414px;
$point_10: 1440px;
//---------------------------------------------media points

//---------------------------------------------forms
$input_height: 3.5rem;
$input_offset: 0 1.4rem;
$input_font_size: $font_size_base;
$input_border_color: #ccc;
$input_border_color_active: $green;
$input_placeholder_color: rgba($black, 0.4);
$input_text_color: $black;
$input_radius: $gl_radius;
//---------------------------------------------forms###
