@mixin cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

@mixin contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

@mixin popup {
	@include flex_block(row, nowrap, flex-start);

	position: fixed;
	top: 0;
	left: 0;

	width: 100vw;
	height: var(--full-screen);
	padding: 2rem 2.7rem;

	overflow-y: auto;

	background-color: rgb(23, 43, 41, 0.5);

	opacity: 0;
	pointer-events: none;
	transition: opacity 0.3s ease; // значение милисекунд должно совпадать с "const timeout" (300) в initPopup.js

	z-index: 1000;

	&.popup--active_state {
		opacity: 1;
		pointer-events: initial;
	}
}
