@import 'ScssHelpers/helpers-all.scss';

.error_section {
	width: 100%;
	min-height: var(--full-screen);
	padding-bottom: var(--height-header);

	background-color: $black_2;

	@include bp($point_4, min) {
		display: flex;
		align-items: center;
		justify-content: center;
		padding-top: var(--height-header);
	}

	@include bp($point_4 - 1) {
		padding-top: 18rem;
		padding-left: 5rem;
		padding-right: 5rem;
	}
}

.error_section__in {
	max-width: 104rem;
}

.error_section__wrap {
	@include bp($point_2, min) {
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.error_section__img_in {
	@include bp($point_2, min) {
		order: 2;
		flex-shrink: 0;
		width: 47%;
	}

	@include bp($point_3) {
		width: 100%;
		max-width: 52rem;
		margin: 0 auto;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}
}

.error_section__pic {
	position: relative;
	display: block;
	width: 100%;

	&::after {
		content: '';
		display: block;
		padding-bottom: 74%;
	}
}

.error_section__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

.error_section__content {
	@include bp($point_2, min) {
		flex-shrink: 0;
		width: 53%;
		order: 1;
		padding: 0 2rem 1.8rem 6rem;
	}

	@include bp($point_3) {
		text-align: center;
	}
}

.error_section__title {
	font-weight: 700;

	letter-spacing: 4px;
	color: $white;

	@include bp($point_2, min) {
		font-size: 5.5rem;
		line-height: 1.2;
	}

	@include bp($point_3) {
		font-size: 4.2rem;
		line-height: 1.57;
		letter-spacing: 2.38px;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.8rem;
		}
	}
}

.error_section__text {
	color: $white;
	line-height: normal;

	@include bp($point_2, min) {
		max-width: 36rem;
		font-size: 2.1rem;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 6.4rem;
		}
	}
}

.error_section__link {
	min-width: 16.4rem;
	text-transform: uppercase;
	@include btn_style_orange_3;
	@include btn_size_normal_2;
}
