@mixin st_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $font_1;

	cursor: pointer;

	&[type='submit'] {
		@include appearance(none);
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color mixins:
@mixin btn_style_white {
	font-weight: 700;
	color: $white;
	background-color: $black_2;
	border: 1px solid $white;
	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover,
	&:focus {
		background-color: $white;
		color: $black_2;
	}
}

@mixin btn_style_white_2 {
	font-weight: 700;
	color: $orange;
	background-color: $white;
	box-shadow: inset 0 0 0 1px $white;

	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover,
	&:focus {
		background-color: $orange;
		color: $white;

		.body--header_green_mod &,
		.body--header_transparent & {
			background-color: $white;
			color: $black_2;
		}
	}

	.body--header_green_mod &,
	.body--header_transparent & {
		background-color: $orange;
		color: $white;
		box-shadow: none;
	}
}

@mixin btn_style_white_3 {
	font-weight: 700;
	color: $white;
	background-color: $black_2;
	border: 1px solid $black_2;
	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover,
	&:focus {
		background-color: $white;
		border: 1px solid $black_2;
		color: $black_2;
	}
}

@mixin btn_style_black {
	font-weight: 700;
	color: $black_2;
	background-color: $white;
	border: 1px solid $black_2;
	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover {
		background-color: $black_2;
		color: $white;
	}
}

@mixin btn_style_orange {
	font-weight: 700;
	color: $orange;
	background-color: $black_2;
	border: 1px solid $orange;
	transition: background-color 0.3s ease, color ease 0.3s,
		border-color ease 0.3s;

	@include st_btn;

	&:hover {
		background-color: $white;
		color: $black_2;
		border-color: $white;
	}
}

@mixin btn_style_orange_2 {
	font-weight: 700;
	color: $white;
	background-color: $orange;
	box-shadow: inset 0 0 0 1px $white;
	transition: background-color 0.3s ease, color ease 0.3s,
		border-color ease 0.3s;

	@include st_btn;

	.body--header_green_mod & {
		background-color: $black_2;
	}

	&:hover,
	&:focus {
		background-color: $white;
		color: $orange;

		.body--header_green_mod & {
			color: $black_2;
			background-color: $white;
		}
	}
}

@mixin btn_style_orange_3 {
	font-weight: 700;
	color: $white;
	background-color: $orange;
	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover,
	&:focus {
		background-color: $white;
		color: $black_2;
	}
}

@mixin btn_style_green {
	font-weight: 700;
	color: $black_2;
	background-color: $green;
	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover {
		background-color: $white;
		color: $black_2;
	}
}

@mixin btn_style_green_2 {
	font-weight: 700;
	color: $white;
	background-color: $black_2;
	border: 1px solid $green;
	transition: background-color 0.3s ease, color ease 0.3s,
		border-color ease 0.3s;

	@include st_btn;

	&:hover {
		background-color: $white;
		color: $black_2;
		border-color: $white;
	}
}

@mixin btn_style_green_3 {
	font-weight: 700;
	color: $black_2;
	background-color: $green;
	border: 1px solid $green;
	transition: background-color 0.3s ease;

	@include st_btn;

	&:hover,
	&:focus {
		background-color: $white;
		&.workspace_wix_tabs__btn {
			border-color: transparent;
		}
	}
}

@mixin btn_style_transparent {
	font-weight: 700;
	color: $white;
	font-family: $font_1;
	background-color: transparent;
	box-shadow: inset 0 0 0 1px $white;
	transition: background-color 0.3s ease, color ease 0.3s,
		border-color ease 0.3s;

	@include st_btn;

	.header__langues:hover &,
	.header__langues:focus & {
		background-color: $white;
		color: $orange;

		.body--header_green_mod &,
		.body--header_transparent & {
			color: $black_2;
		}
	}
}

@mixin btn_style_transparent_2 {
	font-weight: 400;
	color: $black_2;
	background-color: transparent;
	box-shadow: inset 0 0 0 1px $black_2;
	transition: background-color 0.3s ease, color ease 0.3s, box-shadow ease 0.3s;

	@include st_btn;

	@media (any-hover: hover) {
		&:hover {
			background-color: $white;
			color: $black_2;
			box-shadow: inset 0 0 0 1px transparent;
		}
	}
}

//size mixins:
@mixin btn_size_normal {
	height: 4.5rem;
	padding: 0 2rem;

	line-height: 4.5rem;
	font-size: 1.6rem;

	border-radius: 2.7rem;

	.get_started_section &,
	.hero_workspace & {
		@include bp($point_2, min) {
			font-size: 1.8rem;
		}
	}
}

@mixin btn_size_normal_2 {
	height: 4.5rem;
	padding: 0 2rem;

	line-height: 4.5rem;
	font-size: 1.8rem;

	border-radius: 2.7rem;
}

@mixin btn_size_normal_3 {
	height: 4.7rem;
	padding: 0 2.6rem;

	line-height: 4.7rem;
	font-size: 1.6rem;
	letter-spacing: 0.01em;

	border-radius: 4.9rem;
}

@mixin btn_size_small {
	height: 3.5rem;
	padding: 0 1.6rem;

	line-height: 3.5rem;
	font-size: 1.4rem;

	border-radius: 2.7rem;
}
